export default {
    state: {
        url: 'http://overhost.net/rental2/api_v1/ajax/App/request.php',
        cmds: ['getProducts', 'getOrders', 'getMaxOrderID', 'getClients', 'getHistory', 'getLogs'],
        now: new Date(),
        max_order_id: Number,
        new_order_id: Number,

        depositList: {
            1: '',
            2: '',
            3: ' ',
            4: '. ',
            5: '. ',
            6: '',
            7: '',
            8: '',
            9: '',
        },

        promotions: [
            {
                id: 1,
                name: '30%',
                discount: 30
            },
            {
                id: 2,
                name: ' - 10%',
                discount: 10
            }
        ],

        accessories: [
            {
                id: 1,
                name: ' '
            },
            {
                id: 2,
                name: ''
            }
        ]

    },
    mutations: {
        setOpt(state, opt) {
            for (let prop in opt) {
                state[prop] = opt[prop]
            }
            console.log('set options')
        },
        now(state, date) {
            state.now = date
        }
    },
    actions: {
        startTimer({commit}) {
            setInterval(() => {commit('now', new Date())}, 1000)
        }
    },
    getters: {
        now(state) {
            return state.now
        },
        options(state) {
            return state
        },
        depositList(state) {
            return state.depositList
        },
        promotions(state) {
            return state.promotions
        },
        accessories(state) {
            return state.accessories
        }
    }
}


